@import "_global";

.project-card-tooltip-title{
    background-color: rgba(20,20,20,0.5);
    height: 30px;
    font-size: 14px;
    text-transform: uppercase;
    margin: 0 auto;
    letter-spacing: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.project-card-tooltip-subtitle{
    background-color: rgba(150,150,150,0.5);
    color: rgba(225,225,225, 1);
    height: 22px;
    font-size: 12px;
    text-transform: uppercase;
    margin: 0 auto;
    letter-spacing: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.project-card-tooltip-p{
    margin: 7px 10px;
    font-size: 12px;
    font-family: FiraSans;
    letter-spacing: 0.8px;
    line-height: 18px;
    color: var(--monokai-light-gray);
}

.project-card-tooltip-exp{
    display: block;
    margin: 7px 10px;
    font-size: 12px;
    font-family: FiraSans;
    letter-spacing: 0.8px;
    line-height: 18px;
    color: var(--monokai-light-gray);
}

.project-card-tooltip-exp-entry-indent
{
    transform-origin: top;
    display: block;
    margin-left: 20px;
    font-size: 10px;
}

.project-card-tooltip-exp-entry-inline{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.project-card-tooltip-exp-entry{
    display: block;
    margin-bottom: 5px;
}

.project-card-tooltip-types{
    width: 100%;
    height: 36px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.project-card-tooltip-inline-icon{
    width: 12px !important;
    height: 12px !important;
    display: flex;
    align-items: center;
    path{
        fill:white
    }
}

.project-card-tooltip-authors{
    margin: 7px 7px;
    padding-bottom: 7px;
}

.tooltip-type-icon{
    width: 24px !important;
    height: 24px !important;
    path{
        fill:rgba(70,70,70, 1);  
    }
}

.art-icon-active{
    path{
        fill: var(--monokai-red);
    }
}

.machine-learning-icon-active{
    path{
        fill: var(--monokai-orange);
    }
}

.game-icon-active{
    path{
        fill: var(--monokai-yellow);
    }
}

.web-icon-active{
    path{
        fill: var(--monokai-green);
    }
}

.application-icon-active{
    path{
        fill: var(--monokai-blue);
    }
}

.design-icon-active{
    path{
        fill: var(--monokai-purple);
    }
}